<script>
  import { DeckEncoder } from "runeterra";
  import { cardSets } from "./Sets.svelte";

  import qs from "qs";
  import CardBar from "./CardBar.svelte";

  let deckcode = "CEBQCAQDAQCQCBABCETTINQHAEBQEDAPCQPCKKAAAEAQEAYD";
  let url = window.location.href;
  let param = qs.parse(url.split("?")[1]);
  if (param.code) deckcode = param.code;
  let deck = {};
  let heroes = {};
  let minions = {};
  let spells = {};

  decode();
  function decode() {
    deck = DeckEncoder.decode(deckcode);
    deck.forEach((o, i, a) => {
      let set = a[i].set;
      let index = cardSets[set].findIndex(obj => obj.cardCode == a[i].code);
      a[i].supertype = cardSets[set][index].supertype;
      a[i].type = cardSets[set][index].type;
      a[i].cost = cardSets[set][index].cost;
    });
    heroes = deck.filter(card => card.supertype == "英雄");
    minions = deck.filter(card => card.type == "單位" && card.supertype == "");
    spells = deck.filter(card => card.type == "法術");
    sortdeck(heroes);
    sortdeck(minions);
    sortdeck(spells);
  }

  function sortdeck(array) {
    return array.sort(function(a, b) {
      var x = a.cost;
      var y = b.cost;
      return x < y ? -1 : x > y ? 1 : 0;
    });
  }
</script>

<style>
  deckcode {
    width: 90%;
    margin: 1em auto;
    border: 1px solid rgb(73, 52, 31);
    font: 2em;
    display: flex;
    vertical-align: middle;
  }
  label {
    background: rgb(73, 52, 31);
    color: white;
    text-align: center;
    line-height: 200%;
    font-weight: bold;
    padding: 0 1em;
  }
  main {
    margin: 0 auto;
    width: 22em;
  }
  h1 {
    display: block;
    font-size: 1.1em;
    margin: 0 auto;
    text-align: center;
    color: #edc;
    letter-spacing: 0.4em;
  }
  input {
    margin: 0;
    flex-grow: 3;
    padding: 0.5em;
    background: rgb(199, 184, 169);
    color: #321;
  }
  @media only screen and (min-width: 1280px)
  {

    main{
      display:flex;
      width:90%;
      margin:0 auto;
    }
    div{
      margin: 1em;
    }
  }
</style>


<deckcode>
  <label>牌組代碼</label>
  <input on:change={decode} bind:value={deckcode} placeholder="將代碼貼在此" />
</deckcode>

<main>
  <div>
    <h1>英雄: {heroes.length}</h1>
    {#each heroes as card}
      <CardBar {card} />
    {/each}
  </div>
  <div>

    <h1>單位: {minions.length}</h1>
    {#each minions as card}
      <CardBar {card} />
    {/each}
  </div>
  <div>
    <h1>法術: {spells.length}</h1>
    {#each spells as card}
      <CardBar {card} />
    {/each}
  </div>
</main>
